import React from 'react'
import {Link, withRouter,NavLink} from 'react-router-dom';
import OffcanvasMenu from 'react-offcanvas-menu-component'; 
import imgAdmin from './img/admin.png'
import './CSS/index.css'

function MenuCanvas({location}) {
    return (
      <div className="row container-fluid Menu_Container">
        <div className="col-md-2">
            <OffcanvasMenu  className="Menu_Canvas"
			Link={Link} 
			location={location} 
			config={{
				width: 200, 
                push: true, 
                
			}}
                
			menu={[
				{text: 'Home', link: '/'}, 
				{text: 'C nhn', link: '/page', submenu: [ 
					// {text:'n chm cng', link:'/'},
                    {text:'n chm cng', link:'/'},
                    {text:'Bng chm cng', link:'/'},
                    {text:' xut', link:'/'},
                    {text:'KPI', link:'/'},
                    {text:'Bng lng c nhn', link:'/'},
                ]},

                {text: 'Qun l', link: '/page', submenu: [ 
					{text:'Duyt n chm cng', link:'/'},
                    {text:'Duyt  xut', link:'/'},,
                    {text:'Gim st chm cng', link:'/'},
                    {text:'Gim st v tr chm cng', link:'/'}

                ]},

            ]}
            
 
			header={
				<Header />
			}
			// footer={<Footer />} 
		/>
        </div>
            <div className="Menu_content col-md-10 ">
                <div className="Menu_item">
                    <NavLink  to='/focus' activeClassName="main-nav-active"><i className="fa fa-star"></i> Tiu im</NavLink>
                </div>
                <div className="Menu_item">
                    <NavLink to='/calendar' activeClassName="main-nav-active"><i className="fa fa-calendar-alt"></i> Lch biu</NavLink>
                </div>
                <div className="Menu_item">
                    <NavLink to='/chat' activeClassName="main-nav-active"><i className="fa fa-comments"></i> Trao i</NavLink>
                </div>
                <div className="Menu_item">
                    <NavLink to='/notification' activeClassName="main-nav-active"><i className="fa fa-bullhorn"></i> Thng bo</NavLink>
                </div>
            </div>
        </div>
            

    )
}

const Header = () =>{
    return (
        <div>
        <div className="row MenuCanvas_Admin">
            <div className="MenuCanvasAdmin_img col-md-4">
                <img src={imgAdmin} width="60" height="60" alt="admin"/>
            </div>
            <div className="MenuCanvasAdmin_img col-md-8">
                <p>Lnh Do</p>
                <p>Cha chm cng</p>
            </div>
        </div>
          
        </div>
    )
}

